$mobile-width: 800px;

html, body {
	margin: 0;
	padding: 0;
	user-select: none;
	height: 100%;
	width: 100%;
	color: black;
	font-family: Futura BQ, 'Oswald', 'Didact Gothic', sans-serif;
}

body {	
	overflow-x: hidden;
	overflow-y: scroll;
}

a {
	text-decoration: none;
	color: inherit;
	text-decoration-color: inherit;
}

/*OBJECTS*/

.container div.signature, .container div.social, .feed {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	position: relative;
	text-align: center;
	margin: 0 auto;
}

.container .title, .container .footer {
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	position: relative;
	text-align: center;
	margin: 0 auto;
}

.intro {
	text-align: justify;
}

.signature {
	padding: 20px;
	flex-direction: column;
}

.social {
	padding: 20px;
	flex-direction: column;
}

.social div {
	flex-direction: row;
}

.social a img {
	padding: 0 10px;
	height: 40px;
	width: auto;
}

.social a img:hover {
	filter: opacity(75%) sepia(100%) saturate(500%) brightness(70%) hue-rotate(180deg);
}

/*Feed pages*/

div.feed {
	width: 61.8vw;
}

@media only screen and (max-width: $mobile-width) {
	div.feed {
		width: 90vw;
	}
}

.stager {
	width: 100%;
	height: calc(61.8vw * 0.6);
	display: grid;
	grid-template-columns: 31.8% 1fr;
	grid-template-areas:
		'infoBox imageBox';
}

@media only screen and (max-width: $mobile-width) {
	.stager {
		grid-template-columns: unset;
		grid-template-rows: 200px minmax(200px auto);
		grid-template-areas:
			'imageBox'
			'infoBox';
		height: unset;
	}
}

.stager > div {
	width: 100%;
	height: 100%;
}

.stager div.infoBox {
	grid-area: infoBox;
	background-color: white;
	opacity: 0.74;
	display: flex;	
	align-items: center;
	justify-content: center;
}

.stager div.infoBox > div {
	display: block;
	padding: 40px;
	text-align: left;
	font-weight: lighter;
}

.stager div.infoBox > div > div {
	display: inline-flex;
	background-color: orangered;
}

.stager div.infoBox > div > h1 {
	margin: 0;
	font-family: Arial, Helvetica, sans-serif;
	font-size: 2.4em;
	font-weight: bold;
}

.stager div.infoBox > div > div * {
	padding: 10px 30px;
	margin: 0;
	color: white;
}

.stager .imageBox {
	grid-area: infoBox / infoBox / imageBox / imageBox;
	overflow: hidden;
	display: flex;
	align-items: flex-start;
	justify-content: center;
	max-height: 100vh;
}

.imageBox img {
	width: inherit;
	margin-top: -5%;
}

@media only screen and (max-width: $mobile-width) {
	.stager .imageBox {
		grid-area: imageBox / imageBox / infoBox / infoBox;
	}
	.imageBox img {
		padding-top: unset;
		width: auto;
		height: inherit;
	}
}

.italic {
	font-style: italic;
}