div.articles, div.article {
	align-items: flex-start;
	text-align: justify;
	width: 100%;
}

div.article {
	width: 100%;
}

div.article h2 {
	text-align: left;
}

div.article img {
	width: 100%;
}

div.articles h3, div.articles h4, div.articles p {
	margin-block-start: 0.5em;
	margin-block-end: 0.5em;
}

div.articles li {
	margin-block-start: 1.5em;
	margin-block-end: 1.5em;
}