div.navigation {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	width: max-content;
	max-width: 80vw;
	align-self: center;
	margin: 0px auto;
	justify-content: center;
	align-items: center;
	position: relative;
	text-align: center;
	overflow-x: scroll;
	margin-bottom: 1em;
}
div.navigation::-webkit-scrollbar {
	display: none;
  }

.navigation > a {
	opacity: 0.5;
	margin: 1em 15px;
}
.navigation h3 {
	margin: 0;
}

.navigation > a:hover {
	opacity: 0.75;
}